import React from "react";
import { motion } from "framer-motion";

import styles from "./mouse.scss";

export default () => {
  return (
    <motion.div
      variants={{
        initial: { y: 0 },
        animate: {
          y: [0, 10, 10, 10, 0],
          transition: { loop: Infinity, repeatDelay: 1 }
        }
      }}
      initial="initial"
      animate="animate"
      exit="initial"
      className={styles.wrap}
    >
      <div
        variants={{
          initial: { opacity: 0 },
          animate: { opacity: 1 }
        }}
        className={styles.wheel}
      ></div>
    </motion.div>
  );
};
